.header-block {
  padding-top: 30px;
  display: flex;
  justify-content: space-between;
}

.header-block__banner {
  flex: 1 0 50%;
  margin-right: 15px;
}

.header-block__img-slider {
  width: 100%;
  max-width: 540px;
  object-fit: cover;
  object-position: center;
}

.header-product__women-img,
.header-product__men-img {
  width: 100%;
  height: auto;
  max-width: 255px;
  object-fit: cover;
  object-position: center;
}
.product-item__accse-img {
  width: 100%;
  height: auto;
  max-width: 540px;
  object-fit: cover;
  object-position: center;
}

.header-block__banner-slider {
  position: relative;
  max-width: 540px;
}

