}

.mySwiperBigTwo .swiper-button-next {
  display: inline-block;
  position: absolute;
  top: 0 !important;
  right: 14px !important;
  cursor: pointer;
  background-image: url("../../img/banner/arrow-right.png");
  background-repeat: no-repeat;
  background-position: center;
  width: 40px !important;
  height: 40px !important;
  margin-top: 0 !important;
  transform: rotate(90deg);
}

.mySwiperBigTwo .swiper-button-prev {
  display: inline-block;
  position: absolute;
  top: 0 !important;
  left: 17px !important;
  cursor: pointer;
  background-image: url("../../img/banner/arrow-left.png");
  background-repeat: no-repeat;
  background-position: center;
  width: 40px !important;
  height: 40px !important;
  margin-top: 0 !important;
  transform: rotate(90deg);
}

.cart__img-big-two {
  width: 40px;
  height: 40px;
  opacity: 0;
  pointer-events: visible;
}

.mySwiperBig .swiper-button-next {
  display: inline-block;
  position: absolute;
  top: 48.5% !important;
  right: 14px !important;
  transform: translateY(-48.5%) !important;
  cursor: pointer;
  background-image: url("../../img/banner/arrow-right.png");
  background-repeat: no-repeat;
  background-position: center;
  width: 40px !important;
  height: 40px !important;
  margin-top: 0 !important;
}

.mySwiperBig .swiper-button-prev {
  display: inline-block;
  position: absolute;
  top: 48.5% !important;
  left: 17px !important;
  transform: translateY(-48.5%) !important;
  cursor: pointer;
  background-image: url("../../img/banner/arrow-left.png");
  background-repeat: no-repeat;
  background-position: center;
  width: 40px !important;
  height: 40px !important;
  margin-top: 0 !important;
}

.swiper-button-next::after,
.swiper-button-prev::after {
  display: none;
}

